.site-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2000;
    width: 100%;
    padding: 16px $external-gutter;
    background: $color-black;

    @include media-width(lg) {
        padding-top: 32px;
        padding-bottom: 32px;
        background: transparent;
    }
}