@mixin paragraph($size, $include-base-style: false) {

    @if ($include-base-style == true) {
        font-family: $text-font-family;
    }

    @if ($size == large) {
        font-size: 18px;
        font-weight: 100;
        line-height: 2em;

        @include media-width(sm) {
            font-size: 24px;
        }
    }
    @else if ($size == small) {
        font-size: 14px;
        font-weight: normal;
    }
    @else {
        font-weight: 300;
    }
}