.btn {
	display: inline-block;
	padding: 16px 32px;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: .1em;
    text-transform: uppercase;
    background: $color-black get-svg-url(btn-bg, $color-blue-900) left 0 top 100%/100% 0% no-repeat;
    transition: background-size 1.25s $ease-out-expo;
    border-radius: 4px;

    @include hover {
        background-size: 100% 200%;
    }
}

.btn-close {
    position: relative;
    display: inline-block;
    width: 64px;
    height: 64px;
    background: $color-black get-svg-url(btn-bg, $color-blue-900) left 0 top 100%/100% 0% no-repeat;
    transition: background-size .5s $ease-out-expo;
    border-radius: 50%;

    &::before,
    &::after {
        position: absolute;
        top: 50%;
        left: 25%;
        width: 50%;
        height: 2px;
        margin-top: -1px;
        content: "";
        background: $color-white;
        transform: rotate(45deg);
        transition: background-color .25s;
    }

    &::after {
        transform: rotate(-45deg);
    }

    @include hover {
        background-size: 100% 100%;
    }
}

.btn-social {
    display: inline-block;
    width: 48px;
    height: 48px;
    vertical-align: bottom;
    background: $color-black get-svg-url(btn-bg, $color-blue-900) left 0 top 100%/100% 0% no-repeat;
    transition: background-size .5s $ease-out-expo;
    border-radius: 50%;

    @include hover {
        background-size: 100% 100%;
    }
}