.works-list {
    $el: &;

    @include container(small);

    padding: 128px 0;
    list-style: none;

    ol {
        padding: 0;
        margin: 0;
        list-style: none;
    }

    li:not(:last-child) {
        margin-bottom: 64px;
    }

    &__company {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 256px;
        height: 256px;
        margin: 64px auto;
        text-align: center;
        background: none;
        border-radius: 50%;
        border: 2px solid rgba($color-blue-500, .25);
        transition: border-color 1s;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            content: "";
            opacity: var(--percent-y-from-middle, 0);
            border-radius: 50%;
            box-shadow: 0 0 16px rgba($color-blue-900, .5), inset 0 0 16px rgba($color-blue-900, .5);
        }

        svg {
            display: block;
            width: 128px;
            height: 64px;
            margin: 0 auto 16px;
        }

        @include hover {
            transition-duration: 3s;
            transition-delay: .2s;
            border-color: $color-blue-500;
        }
    }

    &__project-link {
        position: relative;
        display: block;

        &::before,
        &::after {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            content: "";
            background: $color-black;
        }

        span {
            display: block;
            overflow: hidden;
        }

        #{$el}__project-logo {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 1;
			display: block;
            width: 40%;
            object-fit: contain;
			transform: translate(-50%, -50%);
        }
    
        #{$el}__project-screenshot {
            max-width: 100%;
            opacity: .33;
            aspect-ratio: 640/370;
        }
    }

    @include media-width(lg, max) {
        &__project-link {
            #{$el}__project-logo {
                opacity: var(--percent-y-from-middle);
            }
        }
    }

    @include media-width(sm) {
        padding-top: 128px;
        padding-bottom: 256px;

        li:not(:last-child) {
            margin-bottom: 128px;
        }

        &__company {
            margin-top: 128px;
            margin-bottom: 128px;
        }
    }

    @include media-width(lg) {
        &__project-link {
            &::before {
                z-index: -2;
                border-radius: 50%;
                box-shadow: 0 0 128px rgba($color-blue-900, .25);
                transition: opacity 1s;
                opacity: 0;
            }

            #{$el}__project-logo {
                opacity: 0;
                transition: opacity 1s;
            }

            @include hover {
                &::before,
                #{$el}__project-logo {
                    opacity: 1;
                    transition-duration: 3s;
                    transition-delay: .2s;
                }
            }
        }
    }
}
