.popin {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3000;
    display: none;
    width: 100%;
    height: 100vh;
    overflow: hidden;

    > div {
        width: 100%;
        height: 100%;
        overflow: auto;
        opacity: 0;
        transform: translateX(20%);
        transition: transform 2s $ease-in-out-expo, opacity 1s;

        > div {
            padding: 64px 32px;
        }
    }

    &__background {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: -1;
        min-width: 100%;
        min-height: 100%;
        fill: $color-black;
        aspect-ratio: .515;
        transform: translate(-50%, -50%) rotate(-180deg); 
    }

    .btn-close {
        display: block;
        margin: 96px auto 0;
        background-image: get-svg-url(btn-bg, $color-white);
        border: 1px solid $color-white;

        @include hover {
            &::before,
            &::after {
                background-color: $color-black;
            }
        }
    }

    &--company {
        h2 {
            svg {
                display: block;
                width: 192px;
                height: 96px;
                margin: 0 auto 64px;
            }
        }
    }

    &.is-open {
        > div {
            opacity: 1;
            transform: translateX(0%);
            transition-delay: 0s, 1s;
        }
    }

    @include media-width(sm) {
        > div {
            > div {
                padding-right: 64px;
                padding-left: 64px;
            }
        }
    }

    @include media-width(md) {
        > div {
            width: 50%;
            margin-left: 50%;

            > div {
                padding: 128px;
            }
        }

        &__background {
            left: 75%;
            min-width: 50%;
        }
    }
}
