.has-smooth-scroll {
    &,
    body {
        overscroll-behavior: none;
        scroll-behavior: auto;
    }

    body {
        height: var(--content-height);
    }

    .site-content {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        transform: translateY(calc(var(--scroll-y) * -1));
    }
}