$color-blue-100: #101820;
$color-blue-300: #233544;
$color-blue-500: #396487;
$color-blue-900: #088AF4;

$color-grey-blue-300: #3B464F;
$color-grey-blue-500: #5E6E7C;

$color-black: #000;
$color-white: #fff;

$color-beapi-primary: #ffe600;
$color-beapi-secondary: #1e1e1e; 

$title-font-family: 'Source Serif 4', 'Serif';
$text-font-family: 'Poppins', 'Sans Serif';

$container-large-width: 1280px;
$container-medium-width: 960px;
$container-small-width: 640px;
$external-gutter: 32px;

$breakpoints: (
    s: 480,
    sm: 768,
    md: 1024,
    lg: 1280,
);