.site-intro {
    display: none;

    .js & {
        position: fixed;
        top: 85px;
        left: 0;
        z-index: 1000;
        display: block;
        width: 100%;
        height: calc(100% - 85px);
        overflow: hidden;

        &:empty {
            background: #000;
        }

        &__background {
            position: absolute;
            top: 50%;
            left: 50%;
            min-width: 100%;
            min-height: 100%;
            fill: $color-black;
            aspect-ratio: 1.94;
            transform: translate(-50%, -50%); 
        }

        @include media-width(md) {
            top: 0;
            height: 100%;
        }
    }
}