.device {
    position: relative;
    background: $color-black;
    border-radius: clamp(2px, .53vw, 16px);

    &--desktop {
        aspect-ratio: 1280/825;

        > * {
            top: 3.87%;
            left: .62%;
            width: 98.75%;
            height: 88.36%;
        }
    }

    &--mobile {
        width: 376px;
        max-width: 100%;
        aspect-ratio: 376/704;
		margin: 0 auto;
		
        > * {
            top: 4.54%;
            left: 2.12%;
            width: 95.74%;
            height: 90.9%;
        }
    }

    img,
    iframe {
        position: absolute;
    }

    img {
        display: block;
        object-fit: cover;
        object-position: center top;;
    }

    iframe {
        border: none;
    }
}