body {
    font-family: $text-font-family;
    line-height: 2.25;
    color: $color-white;
    background: $color-blue-100;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 32px;
    font-family: $title-font-family;
    font-weight: 300;
    line-height: 1.2em;
}

h1 {
    font-size: 58px;
    font-weight: normal;
}

h2 {
    font-size: 38px;
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 24px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 16px;
}

p {
    margin-bottom: 24px;
    font-weight: 300;
}

.is-style-large {
    @include paragraph(large);
}

.is-style-small {
    @include paragraph(small);
}

a {
    color: inherit;
    text-decoration: none;
}

.sr-only {
    position: absolute;
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    margin: -1px !important;
    overflow: hidden !important;
    clip: rect(1px, 1px, 1px, 1px) !important; /* 1 */
    clip-path: inset(50%) !important; /* 2 */
    white-space: nowrap !important; /* 3 */
    border: 0 !important;
}

@include media-width(sm) {
    h1 {
        font-size: 96px;
    }
    
    h2 {
        font-size: 64px;
    }
    
    h3 {
        font-size: 48px;
    }
    
    h4 {
        font-size: 36px;
    }
    
    h5 {
        font-size: 24px;
    }
    
    h6 {
        font-size: 18px;
    }
}