@use "sass:map";

@mixin media-width($breakpoint, $min-or-max-or-breakpoint: min) {
    @if (type-of(map.get($breakpoints, $min-or-max-or-breakpoint)) == "number") {

        @media screen and (min-width: map.get($breakpoints, $breakpoint)) and (max-width: map.get($breakpoints, $min-or-max-or-breakpoint)) {
            @content;
        }
    } @else if ($min-or-max-or-breakpoint == max) {

        @media screen and (max-width: map.get($breakpoints, $breakpoint)) {
            @content;
        }
    } @else {

        @media screen and (min-width: map.get($breakpoints, $breakpoint)) {
            @content;
        }
    }
}
