.logo {
    $el: &;
    $speed-factor: 1;

    position: relative;
    z-index: 1;
    display: block;

    &__name {
        @extend %animated-title;

        margin-bottom: 0;
        font-size: 36px;
        font-weight: 300;
    }

    &__baseline {
        @include paragraph(small);

        margin: 0;
        color: $color-grey-blue-500;
    }

    .js & {
        &__baseline {
            opacity: 0;
        }
    }

    @include media-width(lg, max) {
        &__name {
            font-size: 26px;
        }

        &__baseline {
            font-size: 10px;
        }
    }
}