.work {
    padding: 128px 0 128px;

    &__header {
        @include container(medium);
        margin-bottom: 64px;
    }

    &__chapeau {
        margin: 0;
    }

    h1 {
        @extend %animated-title;
    }

    &__btn {
        margin-top: 32px;
    }

    &__devices {
        position: relative;
        padding-bottom: 5%;
		perspective: 1000px;

        .device--mobile {
            position: absolute;
            right: 5%;
            bottom: 0;
            width: 25%;
            box-shadow: 0 0 80px rgba(0, 0, 0, .5);
            transform: translateY(calc(-15% + (1 - var(--percent-y, 0)) * 30%));
        }

        img {
            opacity: calc(var(--percent-y, 0) * 3);
        }
    }

    &__btn-close {
        margin-top: 64px;
        margin-bottom: 0;
        text-align: center;
    }

    @include media-width(sm) {
        padding-top: 256px;
        padding-bottom: 256px;

        &__header {
            margin-bottom: 128px;
        }

        &__btn {
            margin-top: 64px;
        }

        &__btn-close {
            margin-top: 128px;
        }
    }
}
