@mixin container($size: 'large') {
    $max-width: $container-large-width;

    @if ($size == 'full') {
        $max-width: calc(100% - #{$external-gutter * 2});
    }
    @else if ($size == 'medium') {
        $max-width: $container-medium-width;
    }
    @else if ($size == 'small') {
        $max-width: $container-small-width;
    }

    width: calc(100% - #{$external-gutter * 2});
    max-width: $max-width;
    margin-right: auto;
    margin-left: auto;
}