.has-custom-cursor {

    * {
        cursor: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" xml:space="preserve" fill="%23fff" viewBox="0 0 8 8" width="8" height="8"><circle cx="4" cy="4" r="3"/></svg>'), auto !important;
    }

    *:hover {
        .cursor {
            opacity: 1;
        }
    }

    .cursor {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9999999999999;
        pointer-events: none;
        opacity: 0;
        mix-blend-mode: screen;
        transition: opacity .25s;

        > div {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 24px;
            height: 24px;
            margin: -9px 0 0 -9px;
            background: $color-blue-900;
            border-radius: 50%;
            transition: transform .3s $ease-in-out-cubic;
        }

        &.is-active {
            > div {
                transform: scale(2);
            }

            &[data-class*="btn"] {
                > div {
                    transform: scale(0);
                }
            }

            &[data-tag="iframe"] {
                opacity: 0;

                > div {
                    transform: scale(0);
                }
            }
        }
    }
}
