@use "sass:map";

/**
 * get-svg-url
 *
 * examples :
 *      get-svg-url("arrow")
 *      get-svg-url("arrow", $color-primary)
 *
 */

@function get-svg-url($name: null, $fill: $color-dark, $opacity: 1, $style: "") {

    $svgs: (
        // name-of-the-svg: (viewBox, attributes, content of the svg element)
        "btn-bg": ("0 0 256 64", " preserveAspectRatio='none'", "<path d='M128 0C57.3 0 0 14.3 0 32v32h256V32c0-17.7-57.3-32-128-32z'/>"),
    );

    @if not map-has-key($svgs, $name) {
        @return "";
    }

    @if ($style != "") {
        $style: " style='" + $style + "'";
    }

    @if ($fill != "") {
        $fill: " fill='rgba(#{red($fill), green($fill), blue($fill), $opacity})'";
    }

    @return url("data:image/svg+xml;charset=utf8, %3Csvg xmlns='http://www.w3.org/2000/svg'" + $fill + $style + " viewBox='" + nth(map.get($svgs, $name), 1) + "'" + nth(map.get($svgs, $name), 2) + "%3E%" + nth(map.get($svgs, $name), 3) + "%3C/svg%3E");  /* stylelint-disable-line */
}
